.Background{
	width: 100%;
	height: 100vh;
	background-repeat: no-repeat;
	background-size: cover;
	background-position: center;
	background-blend-mode: multiply;
	
}

.Background h2{
	padding: 0;
	margin:  0;
	text-align: center;
	color: rgba(255,255,255,0.7);
}


